import React, { useState, useEffect } from 'react'
import axios from 'axios'

export default function MediaGallery() {

    const [items, setItems] = useState([]);
    const [selectedImage, setSelectedImage] = useState('');

    useEffect(() => {
        //https://api.flickr.com/services/rest/?method=flickr.interestingness.getList&api_key=ca3783111609d69139840916b7a01ad2&format=json&nojsoncallback=1&per_page=5
        fetch('https://api.flickr.com/services/feeds/photos_public.gne?format=json&nojsoncallback=1&per_page=5')
            .then((response) => response.json())
            .then((data) => {
                console.log(data);
                setItems(data.photos.photo);
            });

        //setSelectedImage(imageURL(resp.data.photos.photo[0]));

    }, [setItems])

    return (
        <div className="media-gallery">
            <h1 className="media-gallery__title">Flickr API Component</h1>
            <div className="media-gallery-thumbnails">
                {items.length ?
                    items.map((item, index) =>
                        <div key={index} onClick={() => setSelectedImage(imageURL(item))}>
                            <img className="media-gallery-thumbnails__img" src={imageURL(item)} />
                        </div>)
                    : <div>Loading...</div>
                }
            </div>
            <div className="media-gallery-main">
                <div>
                    <img className="media-gallery-main__img" src={selectedImage} />
                </div>
            </div>
        </div>
    )
}

// assemble image URL
function imageURL(item: any) {
    return 'http://farm' + item.farm + '.staticflickr.com/' + item.server + '/' + item.id + '_' + item.secret + '.jpg'
}